@import url(https://fonts.googleapis.com/css2?family=Roboto+Slab:wght@500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Source+Sans+Pro&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Source+Sans+Pro&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Source+Sans+Pro&display=swap);
body {
  margin: 0 15px;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: url(/my-weather-app/static/media/bg.db3cfde0.jpeg);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  background-attachment: fixed;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.brandText {
  color: #fff !important;
  text-transform: uppercase;
  font-weight: 600;
}

.brandImg {
  width: 60px;
}

.weather-container {
  border-radius: 10px;
  width: 100%;
  color: white;
  font-family: 'Source Sans Pro', sans-serif;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  padding: 10px 12px;
}

.header {
  -webkit-flex-basis: 100%;
          flex-basis: 100%;
  font-family: 'Source Sans Pro', sans-serif;
}

.header > span {
  font-size: 40px;
  font-weight: 600;
  text-transform: capitalize;
}

.header > h4 {
  font-size: 20px;
}

.weather {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-basis: 100%;
          flex-basis: 100%;
  -webkit-align-items: center;
          align-items: center;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
}

.desc {
  -webkit-flex-basis: 33.33%;
          flex-basis: 33.33%;
  font-size: 16px;
  text-transform: capitalize;
}

.descText {
  display: block;
  margin-top: -18px;
}

.temp {
  -webkit-flex-basis: 33.33%;
          flex-basis: 33.33%;
  font-size: 120px;
}

.detailsContainer {
  -webkit-flex-basis: 33.33%;
          flex-basis: 33.33%;
  background-color: rgba(255, 255, 255, 0.2);
  padding: 20px 0;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  border-radius: 20px;
}

.detailsContainer > article {
  padding: 10px;
  -webkit-flex-basis: 33.33%;
          flex-basis: 33.33%;
  text-align: center;
}

.detailsContainer > article > h4 {
  font-size: 20px;
  margin-bottom: 0;
}

.icon {
  font-size: 70px;
}

.locationIcon {
  font-size: 32px;
}

.loading {
  text-align: center;
  margin-top: 100px;
}

.errorContainer {
  color: white;
  text-align: center;
  font-family: monospace;
  margin-top: 50px;
}

.searchIcon {
  font-size: 40px;
}

@media screen and (max-width: 768px) {
  .desc {
    -webkit-flex-basis: 50%;
            flex-basis: 50%
  }
  
  .temp {
    -webkit-flex-basis: 50%;
            flex-basis: 50%;
    font-size: 85px;
  }

  .detailsContainer {
    -webkit-flex-basis: 100%;
            flex-basis: 100%;
    margin-top: 20px;
  }

  .descText {
    min-width: -webkit-fit-content;
    min-width: -moz-fit-content;
    min-width: fit-content;
    width: 66px;
    text-align: center;
  }

}

@media screen and (max-width: 320px) {
  .temp {
    font-size: 80px;
  }
}

@media screen and (min-width: 728px) {
  .descText {
    font-size: 30px;
  }

  .icon {
    font-size: 80px;
  }

  .temp {
    font-size: 130px;
  }

  .detailsContainer > article > h4 {
    font-size: 25px;
  }
}
.searchContainer {
  margin: 5vw auto;
  padding: 0 12px;
  transition: width 0.8s ease 0.5s;
}

.searchInput {
  padding: 10px 25px;
  border-radius: 25px;
}

.searchInput:focus {
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
}

.searchCityLabel {
  text-align: center;
  color: white;
  font-family: 'Source Sans Pro', sans-serif;
}

@media screen and (min-width: 600px) {
  .searchContainer {
    width: 500px;
  }
}

@media screen and (min-width: 1200px) {
  .searchContainer {
    width: 600px;
  }
}
.App {
  max-width: 1400px;
  margin: 0 auto;
}

footer {
  position: fixed;
  left: 0;
  bottom: 0;
  width: 100%;
  color: white;
  text-align: center;
}

footer > a {
  text-decoration: none;
  color: #0074D9;
}
